<template>
  <div>
    <v-row>
      <v-col
        md="9"
      >
        <material-card
          color="accent"
          full-header
        >
          <template #heading>
            <div
              class="pa-8 white--text text-h4"
            >
              Special Region
            </div>
          </template>
          <v-data-table
            :headers="headers"
            :items="data"
            :items-per-page="10"
            :search="search"
            class="elevation-1 ma-4"
          >
            <template v-slot:item.delivery_fee="{ item }">
              {{ formatThousands(item.delivery_fee, { separator: ',' }) }}
            </template>
            <template v-slot:item.pickup_fee="{ item }">
              {{ formatThousands(item.pickup_fee, { separator: ',' }) }}
            </template>
            <template v-slot:top>
              <v-toolbar
                flat
              >
                <!-- <v-toolbar-title>My CRUD</v-toolbar-title> -->
                <v-text-field
                  v-model="search"
                  width="200"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                  align-right
                />
                <v-spacer />

                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  @click="dialog = true"
                >
                  <v-icon>mdi-plus</v-icon>  Add
                </v-btn>
              </v-toolbar>
            </template>

            <template v-slot:item.action="{ item }">
              <v-btn
                icon
                x-small
                @click="editDialog(item)"
              >
                <v-icon>
                  mdi-pencil
                </v-icon>
              </v-btn>

              <v-btn
                icon
                x-small
                @click="deleteSpecialRegion(item)"
              >
                <v-icon>
                  mdi-delete
                </v-icon>
              </v-btn>
            </template>
          </v-data-table>
        </material-card>
      </v-col>
      <v-col
        class="md-3"
      >
        <material-card
          color="accent"
          full-header
        >
          <template #heading>
            <div
              class="pa-8 white--text text-h4"
            >
              City
            </div>
          </template>
          <v-data-table
            flat
            :headers="headers2"
            :items="special_city"
            :items-per-page="10"
            hide-default-footer
            class="mx-4 mb-7"
          >
            <template v-slot:top>
              <v-toolbar
                flat
              >
                <!-- <v-toolbar-title>My CRUD</v-toolbar-title> -->
                <!-- <v-text-field
                  v-model="search"
                  width="200"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                  align-right
                /> -->
                <v-spacer />

                <v-btn
                  color="primary"
                  dark
                  small
                  class="mb-2"
                  @click="dialog2 = true"
                >
                  <v-icon>mdi-plus</v-icon>  Add
                </v-btn>
              </v-toolbar>
            </template>

            <template v-slot:item.action="{ item }">
              <v-btn
                icon
                x-small
                @click="deleteCity(item)"
              >
                <v-icon>
                  mdi-delete
                </v-icon>
              </v-btn>
            </template>
          </v-data-table>
        </material-card>
      </v-col>
    </v-row>

    <v-dialog
      v-model="dialog"
      max-width="500px"
    >
      <v-card>
        <v-card-title>
          Add Special Region
        </v-card-title>
        <v-card-text>
          <v-select
            v-model="selected_city"
            :items="special_city"
            item-text="nama"
            item-value="city_id"
            label="Select City"
            @change="filterDistrict"
          />
          <v-select
            v-model="selected_district"
            :items="filter_district"
            item-text="nama"
            item-value="id"
            :disabled="selected_city === null"
            label="Select District"
            @change="filterVillage"
          />
          <v-select
            v-model="selected_village"
            :items="filter_village"
            item-text="nama"
            item-value="id"
            :disabled="selected_district === null"
            label="Select Village"
          />
          <vuetify-money
            v-model="delivery_fee"
            label="Delivery Fee"
            :options="options"
            :clearable="clearable"
          />
          <vuetify-money
            v-model="pickup_fee"
            label="Pick up Fee"
            :options="options"
            :clearable="clearable"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            class="float-right"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
          <v-btn
            v-if="id === null"
            color="primary"
            class="float-right"
            small
            text
            @click="saveSpecialRegion"
          >
            Save
          </v-btn>
          <v-btn
            v-else
            color="primary"
            class="float-right"
            small
            text
            @click="updateSpecialRegion"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="dialog2"
      max-width="500px"
    >
      <v-card>
        <v-card-title>
          Add City
        </v-card-title>
        <v-card-text>
          <v-select
            v-model="selected_city"
            :items="city"
            item-text="nama"
            item-value="id"
            label="Select City"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="primary"
            class="float-right"
            text
            @click="dialog2 = false"
          >
            Close
          </v-btn>
          <v-btn
            v-if="id === null"
            color="primary"
            class="float-right"
            small
            text
            @click="saveCity"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
  import axios from 'axios'
  var formatThousands = require('format-thousands')
  export default {
    data () {
      return {
        formatThousands,
        data: [],
        dialog: false,
        dialog2: false,
        city: [],
        district: [],
        village: [],
        special_city: [],
        filter_village: [],
        filter_district: [],
        selected_city: null,
        selected_district: null,
        selected_village: null,
        delivery_fee: 0,
        pickup_fee: 0,
        headers: [
          {
            text: 'Village',
            value: 'village',
          },
          {
            text: 'District',
            value: 'district',
          },
          {
            text: 'City',
            value: 'city',
          },
          {
            text: 'Delivery Fee',
            value: 'delivery_fee',
          },
          {
            text: 'Pick up Fee',
            value: 'pickup_fee',
          },
          {
            text: 'Action',
            value: 'action',
          },
        ],
        headers2: [
          {
            text: 'City',
            value: 'nama',
          },
          {
            text: 'Action',
            value: 'action',
          },
        ],
        search: null,
        id: null,
        outlined: true,
        clearable: true,
        options: {
          locale: 'pt-BR',
          prefix: 'Rp ',
          suffix: ' ',
          length: 12,
          precision: 0,
        },
      }
    },

    mounted () {
      this.getData()
      this.region()
    },

    methods: {
      getData () {
        axios.get('admin/special-region').then((res) => {
          this.data = res.data.data
        })
      },

      region () {
        axios.get('admin/region').then((res) => {
          this.special_city = res.data.special_city
          this.city = res.data.city
          this.district = res.data.district
          this.village = res.data.village
        })
      },

      filterDistrict () {
        this.filter_district = []
        this.filter_district = this.district.filter(
          district => district.kabupaten_id === this.selected_city,
        )
      },

      filterVillage () {
        this.filter_village = []
        this.filter_village = this.village.filter(
          village => village.kecamatan_id === this.selected_district,
        )
      },

      deleteSpecialRegion (item) {
        console.log(item)
        this.$swal({
          title: 'Do you want to delete this data?',
          showCancelButton: true,
          confirmButtonText: 'Yes',
        }).then((result) => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios.get('admin/special-region/delete/' + item.id).then(() => {
              this.$swal('Data Deleted Successfully', '', 'success')
              this.getData()
            }).catch(() => {
              this.$swal('Data delete failed', '', 'error')
            })
          }
        })
      },

      saveSpecialRegion () {
        axios.post('admin/special-region', {
          city_id: this.selected_city,
          village_id: this.selected_village,
          delivery_fee: this.delivery_fee,
          pickup_fee: this.pickup_fee,
          district_id: this.selected_district,
        }).then(() => {
          this.$swal('Data Saved Successfully', '', 'success')
          this.getData()
        })
      },

      editDialog (item) {
        console.log(item.id)
        this.selected_city = item.city_id
        this.filterDistrict()
        this.selected_district = item.district_id
        this.filterVillage()
        this.selected_village = item.village_id
        this.delivery_fee = item.delivery_fee
        this.pickup_fee = item.pickup_fee
        this.id = item.id

        this.dialog = true
      },

      updateSpecialRegion () {
        axios.post('admin/special-region/update', {
          id: this.id,
          city_id: this.selected_city,
          village_id: this.selected_village,
          delivery_fee: this.delivery_fee,
          pickup_fee: this.pickup_fee,
          district_id: this.selected_district,
        }).then(() => {
          this.$swal('Data Saved Successfully', '', 'success')
          this.getData()
          this.id = null
        })
      },

      deleteCity (item) {
        this.$swal({
          title: 'Do you want to delete this data?',
          showCancelButton: true,
          confirmButtonText: 'Yes',
        }).then((result) => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
            axios.get('admin/city/delete/' + item.id).then(() => {
              this.$swal('Data Deleted Successfully', '', 'success')
              this.region()
            }).catch(() => {
              this.$swal('Data delete failed', '', 'success')
            })
          }
        })
      },

      saveCity () {
        axios.post('admin/city', {
          city_id: this.selected_city,
        }).then(() => {
          this.$swal('Data Saved Successfully', '', 'success')
          this.region()
        })
      },

    },
  }
</script>
